.midsectionWrapper {
    width: fit-content;
    display: inline-block;
    margin: 0;
    position: relative;
    padding: 0;
}

.midsectionWrapper img {
    max-width: 100%;
    height: auto;
    vertical-align: bottom;
}

.midsectionWrapper::before {
    content: '';
    height: 100px;
    position: absolute;
    top:0;
    left: 0;
    width: 100%;
    background: linear-gradient(to bottom, #000, transparent);
    
}

