section {
    margin-bottom: 0;
}
.sectionWrapper {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    height: auto;
    background: url("../../assets/Images/section1_background.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    color: #fff;
}

.sectionWrapper::before {
    content: '';
    height: 100px;
    width: 100%;
    top:0;
    left:0;
    position: absolute;
    background: linear-gradient(to bottom, #000, transparent);
}

.subsectionOne {
    display: flex;
    gap: 10%;
    justify-content: center;
    align-items: center;
    padding: 8rem;
}

.subsectionText h3 {
    font-size: 2.5rem;
}

.subsectionText {
    width: 40%;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: start;
}

.subsectionImage {
    width: 40%;

}
.subsectionImage img{
    width: 100%;
    height: 100%;
    object-fit: cover;

}

.subsectionText a {
    background: rgba(165, 18, 47, 0);
    border: rgb(165, 18, 47) 1px solid;
    border-radius: 0.5rem;
    text-decoration: none;
    color: #fff;
    padding: 1rem;
    transition: var(--transition);
}
.subsectionText a:hover
{
background: rgb(165, 18, 47);
}
.subsectionTwo {
    display: flex;
    padding: 8rem;
    justify-content: center;
    align-items: center;
    gap: 10%;
}
hr {
    display: none;
}

/* ================ MEDIA QUERIES ================ */

@media screen and (max-width: 1024px) {
    .sectionWrapper {
        align-items: center;
    }
    .subsectionOne, .subsectionTwo{
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding-top: 2rem;
        padding-bottom: 2rem;
        padding-left: 1rem;
        padding-right: 1rem;
        gap: 1rem;
    }

    .subsectionText {
        text-align: center;
        align-items: center;
        padding: 0;
        width: 100%;
    }

    .subsectionImage {
        width: 20rem;
    }

    hr {
        display: block;
        width: 80%;
        border:1px solid rgb(165, 18, 47);
        background: transparent;
        height: 1px;
        border-radius: 50%;
    }
}